import React, { useState, useEffect } from 'react';
import { AlertCircle, Home, RefreshCw, Mail, Phone } from 'lucide-react';
import "../../Styles/pages/orderFailed.css";
import { Link } from 'gatsby';


function OrderFailed() {



  const [isVisible, setIsVisible] = useState(false);
  const [buttonClicked, setButtonClicked] = useState('');

  useEffect(() => {
    // Trigger entrance animation
    setTimeout(() => setIsVisible(true), 100);
  }, []);

  const handleButtonClick = (buttonType) => {
    setButtonClicked(buttonType);
    setTimeout(() => setButtonClicked(''), 200);
  };

  return (
 <>
    <div className="order-failed-container">
      {/* Navigation Breadcrumb */}
       {/* Header */}
      <header className="header">
        <div className="container">
          <h1 className="page-title">Order Failed</h1>
          <nav className="breadcrumb">
            <a href="#" className="breadcrumb-link">Home</a>
            <span className="breadcrumb-separator">â€º</span>
            <span className="breadcrumb-current">Order Failed</span>
          </nav>
        </div>
      </header>

      {/* Main Content */}
      <div className="main-content">
        <div className={`content-wrapper ${isVisible ? 'slide-up' : ''}`}>
          
          {/* Header */}

          {/* Error Icon with Animation */}
          <div className="icon-container">
            <div className="icon-wrapper">
              <div className="icon-ping"></div>
              <div className="icon-pulse"></div>
              <div className="icon-shake">
                <AlertCircle size={40} className="error-icon" />
              </div>
            </div>
          </div>

          {/* Main Message */}
                   <h1 className="page-title">Order Failed</h1>

            <p className="success-description">
             We're sorry, but there was an issue processing your payment. Your order has not been completed. Please try again or contact our support team for assistance.
            </p>

            {/* Action Button */}
                 <button 
              className={`primary-button ${buttonClicked === 'try-again' ? 'clicked' : ''}`}
              onClick={() => handleButtonClick('try-again')}
            >
              <RefreshCw size={20} className="button-icon" />
              Try Again
            </button>

          
        
          {/* Support Options */}
          <div className="support-section">
            <p className="support-text">Need help? Contact our support team:</p>
            <div className="support-buttons">
              <button className="support-button">
                <Mail size={16} />
                Email Support
              </button>
              <button className="support-button">
                <Phone size={16} />
                Call Us
              </button>
            </div>
          </div>
        </div>
      </div>

    </div>
    <Link to="/ordersuccess" className="toggle-success-failed">
        Toggle Success and Failed
    </Link>
 </>
  );
}

export default OrderFailed